.moviesContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.button {
  background-color: var(--button-bg-color);
  border: none;
  font-family: 'Poppins', sans-serif;
  color: var(--primary-color);
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  cursor: pointer;
}
